<template>
  <div class="setting-psw1 view-background">
    <header-view :title="title" :fixed="fixed" :routerName="routerName"></header-view>
    <section class="psw-content" style="margin-top:.2rem">
      <div class="psw-iput"><label>户名</label><input type="text" placeholder="请输入银行卡户名"></div>
      <div class="psw-iput"><label>账号</label><input type="number" placeholder="请输入银行卡账号"></div>
      <div class="setting-list-item" @click.prevent.stop="visible"><span class="setting-label">所属银行</span><span class="setting-more">{{bank}}<i class="icon iconfont icon-more"></i></span></div>
    </section>
    <mt-popup v-model="popupVisible" position="bottom" style="width:100%">
      <div class="flex-box">
        <div class="type-oper" @click="cacelModal">取消</div>
        <div class="flex-center"></div>
        <div class="type-oper success" @click="selectType">确定</div>
      </div>
      <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
    </mt-popup>
    <div class="setting-psw-btn" @click="confirm">确定</div>
  </div>
</template>

<script>
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        title: '我的银行卡',
        fixed: false,
        routerName: 'userSet',
        param: '',
        popupVisible: false,
        bank: '请选择银行',
        selectedBank: '',
        slots: [{
          flex: 1,
          values: ['邮政银行', '建设银行', '招商银行', ' 邮政银行', '建设银行 ', ' 招商银行'],
          defaultIndex: 0,
          className: 'slots',
          textAlign: 'center'
        }]
      }
    },
    methods: {
      onValuesChange: function (picker, values) {
        if (values[0] === undefined) {
          picker.values[0] = this.slots[0].values[0]
          this.selectedBank = this.slots[0].values[0]
        } else {
          this.selectedBank = values.toString()
        }
      },
      visible: function () {
        this.popupVisible = true
      },
      selectType: function () {
        this.bank = this.selectedBank
        this.popupVisible = false
      },
      cacelModal: function () {
        this.popupVisible = false
      },
      confirm: function () {
        this.$toast('绑定银行卡成功！')
        this.$router.push({
          name: 'userSet'
        })
      }
    }

  }
</script>
<style>
  /*银行卡*/
  
  .flex-box {
    display: flex;
    background-color: #ff9b24;
  }
  
  .type-oper {
    flex: 1;
    text-align: center;
    color: white;
    padding: .1rem 0;
  }
  
  .flex-center {
    flex: 3;
  }
</style>


